var detectFrontEnd = function () {
// Detect OS
    var osName = 'unknown-os';
    var browserName = 'unknown-browser';
    var navAppName = navigator.appName;
    var navAppVersion = navigator.appVersion;
    var navPlatform = navigator.platform;

    if (navPlatform.toUpperCase().indexOf('WIN32') >= 0) osName = 'windows';
    if (navAppVersion.indexOf('Mac') != -1 && navPlatform.toUpperCase().indexOf('MAC') >= 0 && navAppVersion.indexOf('iPhone') == -1 && navAppVersion.indexOf('iPad') < 0 && navAppVersion.indexOf('iPod') < 0 && navPlatform.indexOf('iPhone') < 0 && navPlatform.indexOf('iPad') < 0 && navPlatform.indexOf('iPod') < 0) osName = 'apple-osx';
    if (navAppVersion.match(/(iPhone|iPod|iPad)/i) && navPlatform.match(/(iPhone|iPod|iPad)/i)) osName = 'apple-ios';
    if (navAppVersion.match(/Android/i) && navPlatform.match(/Android/i)) osName = 'android';
    if (navAppVersion.indexOf('X11') != -1) OSName = 'unix';
    if (navAppVersion.indexOf('Linux') != -1) OSName = 'linux';
    if(document.getElementsByTagName('html')[0].className.length > 0) {
        document.getElementsByTagName('html')[0].className += ' ' + osName;
    }
    else {
        document.getElementsByTagName('html')[0].className += osName;
    }
    // Detect iOS device model
    if (osName == 'apple-ios') {
        var iOSdevice = 'unknown-ios-device';
        if (navAppVersion.match(/iPhone/i) && navPlatform.match(/iPhone/i)) iOSdevice = 'iphone';
        if (navAppVersion.match(/iPad/i) && navPlatform.match(/iPad/i)) iOSdevice = 'ipad';
        if (navAppVersion.match(/iPod/i) && navPlatform.match(/iPod/i)) iOSdevice = 'ipod';
        document.getElementsByTagName('html')[0].className += ' ' + iOSdevice;
    }
    // Detect pixel ratio
    if (window.devicePixelRatio > 1) {
        document.getElementsByTagName('html')[0].className += ' retina-display';
    }
    // Detect browser and browser version
    if (navAppName.indexOf('Netscape') != -1 && typeof(navigator.mozContacts) == 'object') {
        browserName = 'firefox';
    };
    if (typeof(navigator.vendor) == 'string') {
        if (navigator.vendor.indexOf('Google') != -1) {
            browserName = 'Chrome';
        };
    }
    if (navAppVersion.indexOf('Edge') != -1) {
        browserName = 'edge';
    };
    if (navAppVersion.indexOf('Trident') != -1) {
        if (navAppVersion.indexOf('rv:11') != -1) {
            browserName = 'ie11';
        }
        else if (navAppVersion.indexOf('MSIE 10.0') != -1) {
            browserName = 'ie10';
        }
        else if (navAppVersion.indexOf('MSIE 9.0') != -1) {
            browserName = 'ie9';
        }
        else if (navAppVersion.indexOf('MSIE 8.0') != -1) {
            browserName = 'ie8';
        }
    };
    document.getElementsByTagName('html')[0].className += ' ' + browserName;
}
detectFrontEnd();
